import React from 'react';
import Loadable from 'react-loadable'

import DefaultLayout from './containers/DefaultLayout';

function Loading() {
  return <div>Loading...</div>;
}

const UserList = Loadable({
  loader: () => import('./views/User/UserList'),
  loading: Loading,
});

const RawMaterialCategory = Loadable({
  loader: () => import('./views/RawMaterialCategory/RawMaterialCategory'),
  loading: Loading,
});

const GoodsCategory = Loadable({
  loader: () => import('./views/GoodsCategory/GoodsCategory'),
  loading: Loading,
});


const Goods = Loadable({
  loader: () => import('./views/Goods/Goods'),
  loading: Loading,
});

const GoodsRawMaterial = Loadable({
  loader: () => import('./views/GoodsRawMaterial/GoodsRawMaterial'),
  loading: Loading,
});


const RawMaterial = Loadable({
  loader: () => import('./views/RawMaterial/RawMaterial'),
  loading: Loading,
});


const GoodsListForOrder = Loadable({
  loader: () => import('./views/CreateOrder/GoodsListForOrder'),
  loading: Loading,
});

const CreateOrder = Loadable({
  loader: () => import('./views/CreateOrder/CreateOrder'),
  loading: Loading,
});

const MyOrderList = Loadable({
  loader: () => import('./views/MyOrderList/MyOrderList'),
  loading: Loading,
});


const routes = [
  { path: '/', exact: true, name: '', component: DefaultLayout },
  { path: '/user/list', name: ' ', component: UserList },
  { path: '/rawMaterialCategory/list', name: '  ', component: RawMaterialCategory},
  { path: '/rawMaterial/list', name: ' ', component: RawMaterial},
  { path: '/goodsCategory/list', name: '  ', component: GoodsCategory},
  { path: '/goods/list', name: ' ', component: Goods},
  { path: '/goodsRawMaterial/list', name: '  ', component: GoodsRawMaterial},
  { path: '/create-order-list', name: ' ', component: GoodsListForOrder},
  { path: '/create-order/:id', name: ' ', component: CreateOrder},
  { path: '/my-orders', name: '  ', component: MyOrderList}
];

export default routes;



